import React, { useState } from "react";
import { saveAs } from "./FileSaver";
import JSZip from "jszip";
import "./../App1.css";

const Home = () => {
    const [searchTerm, setSearchTerm] = useState("");
    const [matchedImages, setMatchedImages] = useState([]);
    const [cart, setCart] = useState([]);

    const handleSearch = (event) => {
        const value = event.target.value;
        setSearchTerm(value);
        if (value.trim() === "") {
            setMatchedImages([]);
        } else {
            const matched = images.filter(image =>
                image.name.toLowerCase().includes(value.toLowerCase())
            );
            setMatchedImages(matched);
        }
    };

    const addToCart = (image) => {
        setCart([...cart, image]);
    };

    const removeFromCart = (image) => {
        const updatedCart = cart.filter(item => item !== image);
        setCart(updatedCart);
    };

    const handleClearCart = () => {
        setCart([]);
    };

    const handleDownload = () => {
        if (cart.length === 0) return;
        const zip = new JSZip();
        const promises = cart.map((image, index) => {
            return fetch(image.url)
                .then((response) => response.blob())
                .then((blob) => {
                    zip.file(`${image.name}_${index}.png`, blob);
                })
                .catch((error) => console.error("Error downloading image:", error));
        });

        Promise.all(promises)
            .then(() => {
                zip.generateAsync({ type: "blob" }).then((content) => {
                    saveAs(content, "cart.zip");
                });
            });
    };

    const handleSingleDownload = (imageUrl, imageName) => {
        fetch(imageUrl)
            .then((response) => response.blob())
            .then((blob) => {
                saveAs(blob, `${imageName}.png`);
            })
            .catch((error) => console.error("Error downloading image:", error));
    };

    const images = [
        { name: "Fido", url: "https://images.rawpixel.com/image_png_800/cHJpdmF0ZS9sci9pbWFnZXMvd2Vic2l0ZS8yMDIzLTA4L3Jhd3BpeGVsb2ZmaWNlMThfaGFwcHlfc21pbGluZ19nb2xkZW5fcmV0cml2ZXJfcHVwcHlfb25fd2hpdGVfYl8xOTAzYWI3Ni04NjQzLTQxNzYtODY3ZS01MjUxNDk1MGExNTMucG5n.png" },
        { name: "Mittens", url: "https://images.unsplash.com/photo-1608848461950-0fe51dfc41cb?q=80&w=1000&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxleHBsb3JlLWZlZWR8MXx8fGVufDB8fHx8fA%3D%3D" },
        { name: "Bubbles", url: "https://t3.ftcdn.net/jpg/05/66/14/16/360_F_566141635_0kJ26Xqbl2fTI1dFQBHJpRBWOM6C5Ryp.jpg" },
        { name: "Milo", url: "https://images.unsplash.com/photo-1425082661705-1834bfd09dca?q=80&w=1000&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8Mnx8aGFtc3RlcnxlbnwwfHwwfHx8MA%3D%3D" }
    ];

    return (
        <div className="container">
            <h1 className="title">Image Download Test</h1>
            <div className="search-container">
                <input
                    type="text"
                    placeholder="Search for an image..."
                    value={searchTerm}
                    onChange={handleSearch}
                    className="search-input"
                />
                <button
                    className="search-button"
                >
                    Search
                </button>
            </div>
            {matchedImages.length > 0 && (
                <div>
                    <h2>Matched Images:</h2>
                    <div className="matched-images">
                        {matchedImages.map((image, index) => (
                            <div key={index} className="image-card">
                                <img src={image.url} alt={image.name} className="image" />
                                <p className="image-name">{image.name}</p>
                                <button
                                    onClick={() => addToCart(image)}
                                    className="add-to-cart-button"
                                >
                                    Add to Folder
                                </button>
                                <button
                                    onClick={() => handleSingleDownload(image.url, image.name)}
                                    className="download-button"
                                >
                                    Download
                                </button>
                            </div>
                        ))}
                    </div>
                </div>
            )}
            <div>
                <h2>Cart</h2>
                <div className="cart-container">
                    {cart.map((image, index) => (
                        <div key={index} className="cart-item">
                            <img src={image.url} alt={image.name} className="image" />
                            <p className="image-name">{image.name}</p>
                            <button
                                onClick={() => removeFromCart(image)}
                                className="remove-from-cart-button"
                            >
                                Remove from Folder
                            </button>
                        </div>
                    ))}
                </div>
                {cart.length > 0 && (
                    <div>
                        <button
                            onClick={handleClearCart}
                            className="clear-cart-button"
                        >
                            Clear Folder
                        </button>
                        <button
                            onClick={handleDownload}
                            className="download-cart-button"
                        >
                            Download Folder
                        </button>
                    </div>
                )}
            </div>
        </div>
    );
};

export default Home;
